/* Services Page Container */
.services-page {
    font-family: 'Segoe UI', sans-serif;
    background-color: #fffdf7;
    color: #333;
  }
  
  /* HeroSection is assumed to already be styled globally */
  
  /* All Services Section */
  .all-services {
    padding: 60px 20px;
    background-color: #fdf7f0;
    text-align: center;
  }
  
  .section-title {
    font-size: 32px;
    color: #8b0000;
    margin-bottom: 10px;
    position: relative;
  }
  
  .section-title::after {
    content: '';
    display: block;
    width: 60px;
    height: 3px;
    background-color: #a0522d;
    margin: 10px auto;
  }
  
  .section-subtitle {
    font-size: 16px;
    color: #555;
    margin-bottom: 40px;
  }
  
  /* Service Grid */
  .services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 30px;
    max-width: 1200px;
    margin: 0 auto;
    text-align: left;
  }
  
  /* Service Info Section */
  .service-info {
    padding: 60px 20px;
    background-color: #fff6ea;
  }
  
  .service-info h3 {
    font-size: 28px;
    text-align: center;
    color: #8b0000;
    margin-bottom: 40px;
  }
  
  /* Info Grid */
  .info-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 30px;
    max-width: 1100px;
    margin: 0 auto;
  }
  
  .info-item {
    background-color: white;
    border: 1px solid #f0dfce;
    padding: 25px 20px;
    border-radius: 10px;
    transition: transform 0.3s, box-shadow 0.3s;
    text-align: center;
  }
  
  .info-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
  }
  
  .info-icon {
    font-size: 36px;
    color: #a0522d;
    margin-bottom: 15px;
  }
  
  /* For Om symbol */
  .vedic-icon {
    font-family: sans-serif;
    font-size: 36px;
    font-weight: bold;
  }
  
  .info-item h4 {
    font-size: 20px;
    margin-bottom: 10px;
    color: #8b0000;
  }
  
  .info-item p {
    font-size: 15px;
    color: #555;
  }
  
  /* Responsive tweaks */
  @media (max-width: 768px) {
    .section-title {
      font-size: 26px;
    }
  
    .service-info h3 {
      font-size: 24px;
    }
  
    .info-item {
      padding: 20px 15px;
    }
  }
  