.footer {
    background-color: #2c3e50;
    color: white;
    padding: 60px 0 0;
  }
  
  .footer-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .footer-row {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
  }
  
  .footer-col {
    padding: 0 15px;
  }
  
  .footer-col h4 {
    font-size: 1.2rem;
    margin-bottom: 25px;
    position: relative;
  }
  
  .footer-col h4::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -10px;
    background: #8e44ad;
    height: 2px;
    width: 50px;
  }
  
  .footer-col p {
    margin-bottom: 20px;
    line-height: 1.6;
  }
  
  .social-links {
    display: flex;
    gap: 15px;
  }
  
  .social-links a {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    color: white;
    transition: all 0.3s ease;
  }
  
  .social-links a:hover {
    background: #8e44ad;
    transform: translateY(-3px);
  }
  
  .footer-col ul {
    list-style: none;
  }
  
  .footer-col ul li {
    margin-bottom: 15px;
  }
  
  .footer-col ul li a {
    color: #bdc3c7;
    transition: all 0.3s ease;
    display: block;
  }
  
  .footer-col ul li a:hover {
    color: white;
    padding-left: 5px;
  }
  
  .contact-info li {
    display: flex;
    align-items: flex-start;
    margin-bottom: 15px;
  }
  
  .contact-info i {
    margin-right: 15px;
    color: #8e44ad;
    font-size: 1.1rem;
    margin-top: 5px;
  }
  
  .contact-info span {
    display: block;
    line-height: 1.6;
  }
  
  .copyright {
    background-color: #1a252f;
    padding: 20px 0;
    margin-top: 40px;
  }
  
  .copyright .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .legal-links {
    display: flex;
    gap: 20px;
  }
  
  .legal-links a {
    color: #bdc3c7;
    transition: all 0.3s ease;
  }
  
  .legal-links a:hover {
    color: white;
  }
  
  @media (max-width: 768px) {
    .copyright .container {
      flex-direction: column;
      text-align: center;
    }
    
    .legal-links {
      margin-top: 15px;
    }
  }