.auth-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000;
  }
  
  .auth-modal-content {
    background: white;
    border-radius: 10px;
    width: 100%;
    max-width: 500px;
    box-shadow: 0 5px 30px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    animation: modalFadeIn 0.3s ease;
  }
  
  @keyframes modalFadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .auth-modal-header {
    padding: 20px;
    background: #8e44ad;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .auth-modal-header h2 {
    margin: 0;
    font-size: 1.5rem;
  }
  
  .close-btn {
    background: none;
    border: none;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 5px;
  }
  
  .auth-modal form {
    padding: 30px;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
    color: #2c3e50;
  }
  
  .form-group input {
    width: 100%;
    padding: 12px 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-family: inherit;
    font-size: 1rem;
    transition: all 0.3s ease;
  }
  
  .form-group input:focus {
    border-color: #8e44ad;
    outline: none;
    box-shadow: 0 0 0 3px rgba(142, 68, 173, 0.2);
  }
  
  .auth-submit-btn {
    background: #8e44ad;
    color: white;
    border: none;
    padding: 12px;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
    transition: all 0.3s ease;
    margin-top: 10px;
  }
  
  .auth-submit-btn:hover {
    background: #9b59b6;
  }
  
  .auth-switch {
    text-align: center;
    margin-top: 20px;
    color: #7f8c8d;
  }
  
  .auth-switch button {
    background: none;
    border: none;
    color: #8e44ad;
    font-weight: 600;
    cursor: pointer;
    padding: 0 5px;
  }
  
  .auth-switch button:hover {
    text-decoration: underline;
  }
  
  .auth-social {
    text-align: center;
    margin-top: 30px;
    padding-top: 20px;
    border-top: 1px solid #eee;
  }
  
  .auth-social p {
    margin-bottom: 15px;
    color: #7f8c8d;
  }
  
  .social-buttons {
    display: flex;
    gap: 10px;
    justify-content: center;
  }
  
  .social-btn {
    display: flex;
    align-items: center;
    padding: 10px 15px;
    border-radius: 5px;
    font-weight: 500;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.3s ease;
    border: 1px solid #ddd;
    background: white;
  }
  
  .social-btn i {
    margin-right: 8px;
    font-size: 1rem;
  }
  
  .social-btn.google {
    color: #db4437;
  }
  
  .social-btn.google:hover {
    background: rgba(219, 68, 55, 0.1);
  }
  
  .social-btn.facebook {
    color: #4267B2;
  }
  
  .social-btn.facebook:hover {
    background: rgba(66, 103, 178, 0.1);
  }
  
  @media (max-width: 576px) {
    .auth-modal-content {
      margin: 0 15px;
    }
    
    .social-buttons {
      flex-direction: column;
    }
    
    .social-btn {
      justify-content: center;
    }
  }